import { Component, OnInit } from '@angular/core';
import {QueryProviderService} from '../../providers/query-provider.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-query',
  templateUrl: './query.component.html',
  styleUrls: ['./query.component.scss']
})
export class QueryComponent implements OnInit {
  search = this._actRouter.snapshot.paramMap.get('id');
  public answData;
  keys = [];
  keysOrigin = [];
  dictionary = {
    'theme': '',
    'teacher': '',
    'name': '',
    'id': 'Id',
    'grupa': '',
    'lastName': '',
    'afterFathName': '-',
    'afterFName': '-',
    'dateOfBirth': ' ',
    'specialityTeacher': '',
    'kategoryT': '',
    'nameTeacher': '"',
    'lastNameTeacher': '',
    'themeDiplom': ' ',
    'occupation': '',
    'hours': ' ',
    'countChildren': '- ',
    'countOfChildren': '- ',
    'salary': '',
    'kafedra': '',
    'stypendia': '',
  };
  constructor(
    private queryProvider: QueryProviderService,
    private _actRouter: ActivatedRoute
  ) { }

  ngOnInit() {
    this.getResult();
  }
  getResult() {
    this.queryProvider.getResultQuery(this.search)
      .subscribe(
        res => {
          this.answData = res;
          this.getKeys();
        },
        err => console.log(err)
      );
  }
  getKeys() {
    console.log(this.answData)
    for (let key in this.answData[0]) {
      this.keys.push(this.dictionary[key]);
      this.keysOrigin.push(key);
    }
  }
}
